import React, { useEffect, useState } from 'react'
import { NavLink } from 'react-router-dom'
import Ownernavbar from '../OwnerNavbar/Ownernavbar';
const Header = () => {
    let [isUserLoggedIn,setIsUserLoggedIn]=useState(false);
    useEffect(()=>{
           if(localStorage.getItem("access-token")){
            setIsUserLoggedIn(true);
           }
    },[])
  return (
    <>
   <header className="header_area">
            <div className="container">
                <nav className="navbar navbar-expand-lg navbar-light">
                    <a className="navbar-brand logo_h" href="index.html"><img src="assets/image/Logo.png" alt=""/></a>
                    <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="icon-bar"></span>
                        <span className="icon-bar"></span>
                        <span className="icon-bar"></span>
                    </button>
                    <div className="collapse navbar-collapse offset" id="navbarSupportedContent">
                        <ul className="nav navbar-nav menu_nav ml-auto">
                            <li className="nav-item "><NavLink className="nav-link" to="/">Home</NavLink></li> 
                            <li className="nav-item"><NavLink className="nav-link" to="/about">About</NavLink></li>
                           
                            <li className="nav-item submenu dropdown">
                                <a href="#" className="nav-link dropdown-toggle" data-toggle="dropdown" role="button" aria-haspopup="true" aria-expanded="false">Blog</a>
                                <ul className="dropdown-menu">
                                    <li className="nav-item"><a className="nav-link" href="blog.html">Blog</a></li>
                                    <li className="nav-item"><a className="nav-link" href="blog-single.html">Blog Details</a></li>
                                </ul>
                            </li> 
                            {
                                isUserLoggedIn?
                                <>
                                <li className="nav-item">
                                <NavLink className="nav-link" to="/myaccount">MyAccount</NavLink></li>
                            <li className="nav-item">
                                <NavLink className="nav-link" to="/logout">Logout</NavLink></li>
                                </>:
                                <> <li className="nav-item">
                                <NavLink className="nav-link" to="/login">userlogin</NavLink>
                                </li>
                            <li className="nav-item">
                                <NavLink className="nav-link" to="/ragistration">userragistration</NavLink></li>
                                </>
                            }
                        </ul>
                    </div> 
                </nav>
            </div>
           
        <Ownernavbar/>
        </header>

   




    </>
  )
}

export default Header