import React, { useEffect, useState } from 'react'
import { NavLink, useNavigate } from 'react-router-dom'
import axios from 'axios'
import { API } from '../../../constant/API'
const HotelMy = () => {
  let n=useNavigate();
  useEffect(()=>{
    if(!localStorage.getItem('access-token-owner')){
        n('/hotels/login');
    }
  })
  let [hotels,setHotels]=useState([]);
  useEffect(()=>{
    axios.get(`${API}hotels`,{headers:{'Authorization':localStorage.getItem('access-token-owner')}})
    .then(response=>{
      setHotels(response.data);
    })
  },[])
  return (
<>
<div className='container 'style={{minHeight:'700px',marginTop:"150px"}}>
    <div className='row'>
        <div className='col-md-12'>
           <NavLink className='btn btn-lg text-light'to='/owner/addhotel'style={{backgroundColor:'#FE080A',}}>Add Your Hotel</NavLink>
           
           <table className='table table-dark table-striped table-hover mt-5'>
            <thead>
              <tr>
                <th>S.NO</th>
                <th>HOTELNAME</th>
                <th>ADDRESS</th>
                <th>CLASS TYPE</th>
                <th>FACILITIES</th>
                <th>NORMAL ROOM</th>
                <th>DELUX ROOM</th>
                <th>LUXARY ROOM</th>
              </tr>

            </thead>
            <tbody>
            {
              hotels.map(c=><tr key={c._id}>
                <td>{c._id}</td>
                <td>{c.hotelname}</td>
                <td>{c.address}</td>
                <td>{c.star}</td>
                <td>{c.facilities}</td>
                <td>{c.normal}</td>
                <td>{c.delux}</td>
                <td>{c.luxary}</td>

              </tr>)
            }
            </tbody>
           </table>
        </div>
    </div>
</div>
</>  )
}

export default HotelMy