import React from 'react'
import { NavLink,useNavigate } from 'react-router-dom'
import { useState,useEffect } from 'react'
import axios from 'axios'
import {useFormik} from 'formik'

import {API} from '../../../constant/API'
import ownerRagistrationSchema from '../../../schemas/ownerragistrationschema'
const Ragistration = () => {

let navigate=useNavigate();


let { handleSubmit, handleChange, touched, errors }=useFormik({
    validationSchema:ownerRagistrationSchema,
    initialValues:{
        name:"",
        username : "",
        password : "",
        hotelname:"",
        repassword : "",
        gender : "",
        contact :"",
        city : "",
        address :""
    },
    onSubmit:(Data)=>{
            console.log(Data)
            axios.post(`${API}owner`,Data).then((response)=>{
            console.log(response.data);
            navigate("/hotels/login");

            })
        }
    })










    let [city,setCity]=useState([]);
    useEffect(()=>{
        axios.get(`${API}city`).then((response)=>{
            setCity(response.data);
        })
    },[])


   

  return (
    
<>

    <div className='container'style={{minHeight:"1000px", marginTop : "100px"}}>
        <form onSubmit={handleSubmit}>
            <div className='row'>
                <div className='col-md-12'>
                    <div className='card card-bodered mt-5'>
                        <div className='card-header bg-primary'>
                            <h1 className='text-light'align="center">
                                <i>OWNER RAGISTRATION</i>
                            </h1>
                        </div>

                    <div className='card-body'>
                        <div className='form-group mb-5'>
                            <label className='form-label'>Name</label>
                            <input type="text" placeholder='name' className={'form-control '+(errors.name && touched.name ?'is-invalid':'')} onChange={handleChange}  name='name' />
                            
                        </div>
                        <div className='form-group mb-5'>
                            <label className='form-label'>username</label>
                            <input type="text"placeholder='username' className={'form-control '+(errors.username&&touched.username?'is-invalid':'')}onChange={handleChange}name='username' />
                            {
                            errors.username&&touched.username?<small className='text-danger'>{errors.username}</small>:''
                            }
                        </div>
                        <div className='form-group mb-5'>
                            <label className='form-label'>hotelname</label>
                            <input type="text"placeholder='hotelname' className={'form-control '+(errors.hotelname&&touched.hotelname?'is-invalid':'')}onChange={handleChange}name='hotelname' />
                            {
                            errors.hotelname&&touched.hotelname?<small className='text-danger'>{errors.hotelname}</small>:''
                            }
                        </div>
                        <div className='form-group mb-5'>
                            <label className='form-label'  >password</label>

                            <input type='password'placeholder='password'    className={'form-control '+(errors.password&&touched.password?'is-invalid':'')} onChange={handleChange}
                        name='password' />
                        
                        {
                                errors.password&&touched.password?<small className='text-danger'>{errors.password}</small>:''
                            }
                        </div>
                        <div className='form-group mb-5'>
                            <label className='form-label'>Re-Password</label>
                        <div className='input-group'>

                            <input type='password'placeholder='Re-Password' name='repassword' className={'form-control '+(errors.repassword&&touched.repassword?'is-invalid':'')}
                            onChange={handleChange}  />
                        
                        </div>
                        {
                            errors.repassword&&touched.repassword?<small className='text-danger'>{errors.repassword}</small>:''
                            }
                        </div>
                        <div className='form-group mb-5'>
                            <label className='form-label'>Gender</label>
                            <br />
                            <br />

                        Male <input type='radio'name='gender'value='male'onChange={handleChange} />
                        Female <input type='radio'name='gender' value='female' onChange={handleChange}  />
                        {
                            errors.gender&&touched.gender?<small className='text-danger'>{errors.gender}</small>:''
                            }

                        
                        </div>
                        <div className='form-group mb-5'>
                            <label className='form-label'>City</label>
                            <select className={'form-control '+(errors.city&&touched.city?'is-invalid':'')}onChange={handleChange} name='city'>
                                <option >choose your city</option>
                            {
                                city.map((c)=>{
                                    return(
                                        <>
                                    <option key={c._id}>  {c.name}({c.state})</option> 
                                        </>
                                    )
                                })
                            }

                            </select>
                            {
                            errors.city&&touched.city?<small className='text-danger'>{errors.city}</small>:''
                            }
                        </div>
                        <div className='form-group mb-5'>
                            <label className='form-label'>Adderess</label>
                            <textarea  className={'form-control '+(errors.address&&touched.address?'is-invalid':'')}name='address'onChange={handleChange} ></textarea>
                            {
                            errors.address&&touched.address?<small className='text-danger'>{errors.address}</small>:''
                            }
                        </div>
                        <div className='form-group mb-5'>
                            <label className='form-label'>CONTACT</label>
                            <input type='text'placeholder='contact'className={'form-control '+(errors.contact&&touched.contact?'is-invalid':'')} name='contact'onChange={handleChange} />
                            {
                            errors.contact&&touched.contact?<small className='text-danger'>{errors.contact}</small>:''
                            }
                        </div>
                    </div>

                        <div className='card-footer mb-5'align='center'>
                            <button type='submit' className='btn btn-primary btn-block'><i>SIGNUP</i></button>
                        </div>
                        <br />
                        <small>Already a custmor <NavLink to='/hotels/login'><span className='text-primary'>Login Here</span></NavLink></small>
                    </div>
                </div>
            </div>
        </form>
</div>


</>  )
}

export default Ragistration