import React from 'react'
import { NavLink,useNavigate } from 'react-router-dom'
import axios from 'axios'
import { useState,useEffect } from 'react'
import { API } from '../../../constant/API'
import {useFormik} from 'formik'
import UserRagistrationSchema from '../../../schemas/UserRagistrationSchema'

const Ragistration = () => {
    let navigate=useNavigate();
let {handleChange,handleSubmit,errors,touched}=useFormik({
    validationSchema:UserRagistrationSchema,
initialValues:{
    fullname:"",
    username:"",
    password:"",
    repassword:"",
    gender:"",
    contact:"",
    city:"",
    address:""

},onSubmit:(formdata)=>{
    axios.post(`${API}user`,formdata).then((response)=>{
        console.log(response.data);
        navigate("/login");
    })
}
})





    let [city,setCity]=useState([]);
    useEffect(()=>{
        axios.get(`${API}city`).then((response)=>{
            console.log(response.data)
            setCity(response.data);
        })
    },[])
  return (
<>
<div className='container'style={{minHeight:"300px"}}></div>

<div className='container'style={{minHeight:"1000px"}}>
    <form onSubmit={handleSubmit}>
    <div className='row'>
        <div className='col-md-12 '>



<div className='card card-bodered'>

<div className='card-header bg-dark'><i className="fa fa-user-plus fa-5x text-light" aria-hidden="true"></i>
    <h1 className='display-4 text-light'align="center">
        <i>USER RAGISTRATION</i>
        </h1>
        </div>

<div className='card-body'>


<div className='form-group mb-5'>
    <label className='form-label'>FullName</label>
    <input type="text"placeholder='fullname'className={'form-control '+(errors.fullname&&touched.fullname?'is-invalid':'')}onChange={handleChange}name='fullname' />
</div>

{
    errors.fullname&&touched.fullname?<small className='text-danger'>{errors.fullname}</small>:''
}

<div className='form-group mb-5'>
    <label className='form-label'>Username</label>
    <input type="text"placeholder='Username'className={'form-control '+(errors.username&&touched.username?'is-invalid':'')}onChange={handleChange}name='username' />
</div>

{
    errors.username&&touched.username?<small className='text-danger'>{errors.username}</small>:''
}



<div className='form-group mb-5'>
    <label className='form-label'>Password</label>
<div className='input-group'>

    <input type="password"placeholder='password'className={'form-control '+(errors.password&&touched.password?'is-invalid':'')}onChange={handleChange}name='password'  />
    <button className='btn btn-outline-dark'><i className="fa fa-eye" aria-hidden="true"></i>

    </button>
</div>
</div>
{
    errors.password&&touched.password?<small className='text-danger'>{errors.password}</small>:''
}



<div className='form-group mb-5'>
    <label className='form-label'>Re-Password</label>
<div className='input-group'>

    <input  type="password"placeholder='Re-Password'className={'form-control '+(errors.repassword&&touched.repassword?'is-invalid':'')}onChange={handleChange}name='repassword' />
    <button className='btn btn-outline-dark'><i className="fa fa-eye" aria-hidden="true"></i>

    </button>
</div>
</div>
{
    errors.repassword&&touched.repassword?<small className='text-danger'>{errors.repassword}</small>:''
}



<div className='form-group mb-5'>
    <label className='form-label'>Gender</label>
    <br />
    <br />

   Male <input type="radio"name='gender'value='male' onChange={handleChange} />
  Female <input type="radio"name='gender'value='female'  onChange={handleChange} />

   
</div>

{
    errors.gender&&touched.gender?<small className='text-danger'>{errors.gender}</small>:''
}


<div className='form-group mb-5'>
    <label className='form-label' >City</label>
    <select className={'form-control '+(errors.city&&touched.city?'is-invalid':'')}onChange={handleChange}name='city' >
        <option >choose your city</option>
      {
        city.map((c)=>{
            return(
                <>
                <option key={c._id}>{c.name}</option>
                </>
            )
        })
      }
    </select>
    {
    errors.city&&touched.city?<small className='text-danger'>{errors.city}</small>:''
}

</div>



<div className='form-group mb-5'>
    <label className='form-label'>Adderess</label>
    <textarea  className={'form-control '+(errors.address&&touched.address?'is-invalid':'')}onChange={handleChange}name='address' ></textarea>
</div>

{
    errors.address&&touched.address?<small className='text-danger'>{errors.address}</small>:''
}



<div className='form-group mb-5'>
    <label className='form-label'>CONTACT</label>
    <input type="text"placeholder='contact'className={'form-control '+(errors.contact&&touched.contact?'is-invalid':'')}onChange={handleChange}name='contact' />
</div>

{
    errors.contact&&touched.contact?<small className='text-danger'>{errors.contact}</small>:''
}











</div>

<div className='card-footer bg-dark mb-5'align="center">
    <button className='btn btn-dark'type='submit'>
        SIGNUP</button>
</div>










<br />
<small>Already a custmor <NavLink to='/login'><span className='text-primary'>Login Here</span></NavLink></small>


</div>



        </div>
    </div>
    </form>
</div>

</>


    )
}

export default Ragistration