import { useFormik } from 'formik'
import React, { useState } from 'react'
import axios from 'axios'
import { NavLink, useNavigate } from 'react-router-dom'
import Ownerloginschema from '../../../schemas/OwnerLoginSchema'
import { API } from '../../../constant/API'

const Login = () => {
    let nav=useNavigate();
    let [errMsg,setErrMsg]=useState("")
    let {handleChange,handleSubmit,touched,errors}=useFormik({
        validationSchema:Ownerloginschema,
        initialValues:{
            username:"",
            password:""

        },onSubmit:(Data)=>{
        axios.post(`${API}owner/auth`,Data).then(response=>{
              if(response.data.success==true){
                let token=response.data.token;
                localStorage.setItem("hotelname",response.data.hotelname);
                localStorage.setItem("access-token-owner",token);
                nav("/owner/dashboard");
              }

            else{
                if(response.data.errType==1){
                  setErrMsg("This username and password is not found");
                }
                if(response.data.errType==2){
                    setErrMsg("This password is not correct");
                }
            }
        })
        }
    })
  return (
<>
<div className='container'style={{minHeight:"300px"}}></div>

<div className='container mt-5'style={{minHeight:"1500px",marginTop:"500px"}}>
<form onSubmit={handleSubmit} >

    <div className='row'>
        <div className='col-md-6 offset-md-3'>
{
      errMsg?<div className='alert alert-danger'>{errMsg}<button data-dismiss='alert'className='close'>x</button></div>:''
}

<div className='card card-bodered mt-5'>
    <div className='card-header bg-primary'>
        <h1 className='text-light'><i>ownerlogin</i></h1>

    </div>

<div className='card-body'>
<div className='form-group mb-5'>
    <label className='form-label'>username</label>
    <input type="text"className={'form-control '+(errors.username&&touched.username?'is-invalid':'')} name='username'onChange={handleChange}/>
</div>
{
    errors.username&&touched.username?<small className='text-danger'>{errors.username}</small>:''
}

<div className='form-group mb-5'>
    <label className='form-label'>password</label>
    <input type="password"className={'form-control '+(errors.password&&touched.password?'is-invalid':'')} name='password'onChange={handleChange} />
</div>

{
    errors.password&&touched.password?<small className='text-danger'>{errors.password}</small>:''
}






</div>



<div className='card-footer'>
    <button className='btn btn-primary'type='submit'>login</button>
</div>


<br />
<br />
<small>Already a custmor <NavLink to='/hotels/ragistration'><span className='text-primary'>Ragistration Here</span></NavLink></small>





</div>











        </div>
    </div>        </form>

</div>


</>  )
}

export default Login