import React from 'react'

const Hotels = (props) => {
  return (
<>
<div className="col-lg-3 col-sm-6">
                        <div className="accomodation_item text-center">
                            <div className="hotel_img">
                                <img src={'/assets/images/destination-1.jpg'}style={{height:'400px'}} />
                                <a href="#" className="btn theme_btn button_hover">Book Now</a>
                            </div>
                            <a href="#"><h4 className="sec_h4">{props.info.address}</h4></a>
                            <h5>{props.info.hotelname}<small>/night</small></h5>
                        </div>
                    </div>


</>  )
}

export default Hotels