import React from 'react'
import { useEffect } from 'react';
import { useState } from 'react'
import {NavLink} from 'react-router-dom'

const Ownernavbar = () => {
    let [check,setCheack]=useState(false);
    useEffect(()=>{
    if(localStorage.getItem("access-token-owner")){
        setCheack(true);
    }
    },[])
  return (
    <div className='container-fluid'style={{backgroundColor:"#FE080A"}}>
    <div className='container'>
 <ul className='nav justify-content-end'>
    {
        check?
        <>
       <li className='nav-item dropdown'>
         <a href="#"className='nav-link dropdown-toggle text-light text-weight-bold'data-toggle='dropdown'>
            {
               localStorage.getItem("hotelname")
            }
         </a>
        <div className='dropdown-menu'>
        <NavLink to='/owner/dashboard'className='dropdown-item  '>
          <span className='text-dark'> Dashboard</span>
          </NavLink>
          <NavLink to='/owner/myhotel'className='dropdown-item '>
          <span className='text-dark'> MyHotels</span>
          </NavLink>
          <NavLink to='/owner/logout'className='dropdown-item'>
          <span className='text-dark'>Logout</span>
          </NavLink>
        </div>
       
    </li>  
               
   
        </>:
        <><li className='nav-item'>
        <NavLink to='/hotels/login'className='nav-link text-dark '>
           <span className='text-light'><i className="fa fa-map-marker" aria-hidden="true"></i>   list your hotel</span>
           </NavLink>
     </li>
        </>
    }
 </ul>
 {
   localStorage.getItem("access-token") ?   <span className='navbar-text text-dark badge badge-light'style={{fontSize:'20px',marginTop:'-100px',padding:'40px'}}> {localStorage.getItem("name")}</span>
   :''
 }
       
       </div></div>    
 
 
  )
}

export default Ownernavbar