import * as YUP from 'yup';

let ownerRagistrationSchema=YUP.object({
    name:   YUP.string().required("INSERT YOUR NAME"),
    username: YUP.string().email("Please Type Correct Email Id").required("INSERT YOUR EMAIL"),
    password: YUP.string().matches(/^(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{5,}$/,{message:"PLEASE MAKE STRONG PASSWORD AT LEASTE 3 UPPER AND LOWER CHARACTER"}).required("INSERT YOUR PASSWORD"),
    repassword:YUP.string().oneOf([YUP.ref('password')],"PASSWORD AND CONFORM PASSWORD SHOULD BE MATCH").required("INSERT YOUR REPASSWORD"),
    gender:YUP.string().required("CHOOSE YOUR GENDER"),
    contact:YUP.number().typeError("PLEASE INTER YOUR CORRECT MOBILE NUMBER").min(100000000,"PLEASE ENTER MINIMUM 10 DIGIT NUMBER").max(9999999999,"PLEASE ENTER MAXIMUM 10 DIGIT NUMBER").required("INSERT YOUR CONTACT NUMBER"),
    city:YUP.string().required("INSERT YOUR CITY"),
    address:YUP.string().required("INSERT YOUR ADDRESS"),
    hotelname:YUP.string().required("INSERT YOUR HOTELNAME")

});
export default ownerRagistrationSchema;

