import React, { useState,useEffect } from 'react'
import Blog from '../../sherd/Blog/Blog'
const Help = () => {
    let [Pass,setPass]=useState("password");
    let fun=()=>{
        if(Pass=="password")
        setPass("text")
        else
        setPass("password")
    }
    let [count,setCount]=useState("0")
let demo=(a)=>{
if(10-a.target.value.length>=0){
    setCount(10-a.target.value.length);
}
}

let [age,setAge]=useState(25);
let [name,setName]=useState("rohit");
useEffect(()=>{
    console.log("***********");
})
let demo11=()=>{
    setAge("30")
}
let demo12=()=>{
    setName("gaurav")
}







  return (






<>
<div className='container 'style={{marginTop:"200px"}}></div>
<div className='container my-5'style={{minHeight:"700px"}}>

<h1>{count}</h1>
<label className='form-label'>password</label>
<input type="text" />

<div className='input-group'>

<input type={Pass} onKeyUp={(event)=>demo(event) } className='form-control' />
<button onClick={fun} className='btn btn-outline-danger bg-dark'>
{
    Pass=="password"?<i class="fa fa-eye-slash" aria-hidden="true"></i>:<i class="fa fa-eye" aria-hidden="true"></i>

}

</button>

</div>
<h1>hello</h1>
<h1>{age}</h1>
<h1>{name}</h1>
<button onClick={demo12} className='btn btn-dark'>ok</button>
<button onClick={demo11} className='btn btn-dark'>ok</button>


</div>




<Blog/>

</>  )
}

export default Help