import React from 'react'
import Hotels from '../Hotels/Hotels'
import axios from 'axios'
import { useState,useEffect } from 'react'
import {API} from '../../../constant/API'

const Banner = () => {
    let [showDiv,setShowDiv]=useState(false);
    let [filtered,setfiltered]=useState([]);
    let[data,setData]=useState([]);
    let [city,setCity]=useState([]);
    let [searchText,setSearchText]=useState("");

    useEffect(()=>{
        axios.get(`${API}city`).then(response=>{
            setCity(response.data);
        })
    },[])
    let search=(e)=>{
        let a=e.target.value;
        if(a.length>0){
            setShowDiv(true);
            let temp=a.split("");
            let firstletter=temp[0].toUpperCase();
            temp.splice(0,1);
            let remainString=temp.join("");
            remainString=remainString.toLowerCase();
            let finalString=firstletter+remainString;
            let arr=city.filter(value=>value.name.startsWith(finalString));
            setfiltered(arr);
        }
        setSearchText(e.target.value);
    }
       let selectWord=(e)=>{
        console.log(e.target.innerHTML);
        setSearchText(e.target.innerHTML);
        setShowDiv(false);
       }
        useEffect(()=>{
            axios.get(`${API}hotels`,).then((response)=>{
                setData(response.data);
            })
        },[]) 

  return (
<>
<section className="banner_area">
            <div className="booking_table d_flex align-items-center">
            	<div className="overlay bg-parallax" data-stellar-ratio="0.9" data-stellar-vertical-offset="0" data-background=""></div>
				<div className="container">
					<div className="banner_content text-center">
						<h6>Away from monotonous life</h6>
						<h2>Relax Your Mind</h2>
						<p>If you are looking at blank cassettes on the web, you may be very confused at the<br/> difference in price. You may see some for as low as $.17 each.</p>
						<a href="#" className="btn theme_btn button_hover">Get Started</a>
					</div>
				</div>
            </div>
            <div className="hotel_booking_area position">
                <div className="container">
                    <div className="hotel_booking_table">
                        <div className="col-md-3">
                            <h2>Book<br/> Your Room</h2>
                        </div>
                        <div className="col-md-9">
                            <div className="boking_table">
                                <div className="row">
                                    <div className="col-md-4">
                                        <div className="book_tabel_item">
                                            <div className="form-group">
                                                <div className='input-group date' id='datetimepicker11'>
                                                    <input type='text' className="form-control" placeholder="Arrival Date"/>
                                                    <span className="input-group-addon">
                                                        <i className="fa fa-calendar" aria-hidden="true"></i>
                                                    </span>
                                                </div>
                                            </div>
                                            <div className="form-group">
                                                <div className='input-group date' id='datetimepicker1'>
                                                    <input type='text' className="form-control" placeholder="Departure Date"/>
                                                    <span className="input-group-addon">
                                                        <i className="fa fa-calendar" aria-hidden="true"></i>
                                                    </span>
                                                </div>
                                            </div>
                                            <div className="form-group">
                                                <div className='input-group' id='datetimepicker11'>
                                                    <input type='text' className="form-control" placeholder="Location"value={searchText}onChange={(e)=>search(e)}/>
                                                    <span className="input-group-addon">
                                                    <i className="fa fa-map-marker" aria-hidden="true"></i>
                                                    </span>

                                                </div>
                                                <div style={{backgroundColor:'white',width:'247px',height:'250px',zIndex:'9999',overflowY:'scroll',display:showDiv?'block':'none',marginTop:'-7px',position:'absolute'}}value={searchText}onChange={(e)=>search(e)}>
                                                    <ul style={{margin:'5px'}}>
                                                        {
                                                            filtered.map(c=><li key={c._id} style={{cursor:"pointer"}}onClick={(e)=>selectWord(e)}>{c.name}</li>)
                                                        }
                                                    </ul>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="col-md-4">
                                        <div className="book_tabel_item">
                                            <div className="input-group">
                                                <select className="wide">
                                                    <option data-display="Adult">Adult</option>
                                                    <option value="1">Old</option>
                                                    <option value="2">Younger</option>
                                                    <option value="3">Potato</option>
                                                </select>
                                            </div>
                                            <div className="input-group">
                                                <select className="wide">
                                                    <option data-display="Child">Child</option>
                                                    <option value="1">Child</option>
                                                    <option value="2">Baby</option>
                                                    <option value="3">Child</option>
                                                </select>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="col-md-4">
                                        <div className="book_tabel_item">
                                            <div className="input-group">
                                                <select className="wide">
                                                    <option data-display="Child">Number of Rooms</option>
                                                    <option value="1">Room 01</option>
                                                    <option value="2">Room 02</option>
                                                    <option value="3">Room 03</option>
                                                </select>
                                            </div>
                                            <a className="book_now_btn button_hover" href="#">Search Hotel</a>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
       
        <section className="accomodation_area section_gap">
            <div className="container">
                <div className="section_title text-center">
                    <h2 className="title_color">Hotel Accomodation</h2>
                    <p>We all live in an age that belongs to the young at heart. Life that is becoming extremely fast, </p>
                </div>
                <div className="row mb_30">
               {
                data.map((x)=>{
                    return(
                   <Hotels key={x._id} info={x}/>    
      

                    )
                })
               }
                </div>
            </div>
        </section>
        <section className="facilities_area section_gap">
            <div className="overlay bg-parallax" data-stellar-ratio="0.8" data-stellar-vertical-offset="0" data-background="">  
            </div>
            <div className="container">
                <div className="section_title text-center">
                    <h2 className="title_w">Royal Facilities</h2>
                    <p>Who are in extremely love with eco friendly system.</p>
                </div>
                <div className="row mb_30">
                    <div className="col-lg-4 col-md-6">
                        <div className="facilities_item">
                            <h4 className="sec_h4"><i className="lnr lnr-dinner"></i>Restaurant</h4>
                            <p>Usage of the Internet is becoming more common due to rapid advancement of technology and power.</p>
                        </div>
                    </div>
                    <div className="col-lg-4 col-md-6">
                        <div className="facilities_item">
                            <h4 className="sec_h4"><i className="lnr lnr-bicycle"></i>Sports CLub</h4>
                            <p>Usage of the Internet is becoming more common due to rapid advancement of technology and power.</p>
                        </div>
                    </div>
                    <div className="col-lg-4 col-md-6">
                        <div className="facilities_item">
                            <h4 className="sec_h4"><i className="lnr lnr-shirt"></i>Swimming Pool</h4>
                            <p>Usage of the Internet is becoming more common due to rapid advancement of technology and power.</p>
                        </div>
                    </div>
                    <div className="col-lg-4 col-md-6">
                        <div className="facilities_item">
                            <h4 className="sec_h4"><i className="lnr lnr-car"></i>Rent a Car</h4>
                            <p>Usage of the Internet is becoming more common due to rapid advancement of technology and power.</p>
                        </div>
                    </div>
                    <div className="col-lg-4 col-md-6">
                        <div className="facilities_item">
                            <h4 className="sec_h4"><i className="lnr lnr-construction"></i>Gymnesium</h4>
                            <p>Usage of the Internet is becoming more common due to rapid advancement of technology and power.</p>
                        </div>
                    </div>
                    <div className="col-lg-4 col-md-6">
                        <div className="facilities_item">
                            <h4 className="sec_h4"><i className="lnr lnr-coffee-cup"></i>Bar</h4>
                            <p>Usage of the Internet is becoming more common due to rapid advancement of technology and power.</p>
                        </div>
                    </div>
                </div>
            </div>
        </section>

</>



    )
}

export default Banner