import React from 'react'
import { NavLink, useNavigate } from 'react-router-dom'
import { API } from '../../../constant/API'
import {useFormik} from 'formik'
import { useState } from 'react'
import userloginschema from '../../../schemas/UserLoginSchema'
import axios from 'axios'

const Login = () => {
    let nav=useNavigate();
    let [errmsg,setErrMsg]=useState("");
let {handleChange,handleSubmit,errors,touched}=useFormik({
    validationSchema:userloginschema,
    initialValues:{
        username:"",
        password:""
    },
    onSubmit:(formdata)=>{
        axios.post(`${API}user/auth`,formdata).then((response)=>{
            if(response.data.success==true){
              let token=response.data.token;
              localStorage.setItem("access-token",token);
              localStorage.setItem("name",response.data.name);
              nav("/myaccount");
            }else{
                if(response.data.errType==1){
                    setErrMsg("this username and password is incorrect");
                }
                if(response.data.errType==2){
                    setErrMsg("this password are not found");
    }
}


        })
    }
})

  return (

<>
<div className='container'style={{minHeight:"300px"}}></div>

<div className='container mt-5'style={{minHeight:"700px"}}>
    <form onSubmit={handleSubmit}>
    <div className='row'>
        <div className='col-md-6 offset-md-3'>
            {
                errmsg?<div className='alert alert-danger'>{errmsg}<button data-dismiss='alert'className='close'>x</button></div>:''
            }


                <div className='card card-bodered mt-5'>
                    <div className='card-header bg-dark'>
                        <h1 className='text-light'><i>userlogin</i></h1>

                    </div>

                <div className='card-body'>
                <div className='form-group mb-5'>
                    <label className='form-label'>username</label>
                    <input type='text'name='username' className={'form-control '+(errors.username&&touched.username?'is-invalid':'')}onChange={handleChange} />
                </div>
                {
                    errors.username&&touched.username?<small className='text-danger'>{errors.username}</small>:''
                }

                <div className='form-group mb-5'>
                    <label className='form-label'>password</label>
                    <input type="password"className={'form-control '+(errors.password&&touched.password?'is-invalid':'')}onChange={handleChange} name='password'/>
                </div>

                {
                    errors.password&&touched.password?<small className='text-danger'>{errors.password}</small>:''
                }





                </div>



                <div className='card-footer'>
                    <button className='btn btn-dark'type='submit'>login</button>
                </div>


                <br />
                <br />
                <small>Already a custmor <NavLink to='/ragistration'><span className='text-primary'>Ragistration Here</span></NavLink></small>





                </div>











        </div>
    </div>
    </form>
</div>

</>


    )
}

export default Login