import React from 'react'
import Banner from '../../sherd/Banner/Banner'
import axios from 'axios';
import { useState } from 'react'
import { useEffect } from 'react';
const Contact = () => {
  let [student,setStudent]=useState([]);
  useEffect(()=>{
    axios.get("http://localhost:8080/student").then((response)=>{
      setStudent(response.data);
    })
  },[])
  return (

<>
<h1>hello</h1>
<div className='container'style={{minHeight:"650px"}}></div>
<div className='container'style={{minHeight:"700px"}}>
  <div className='row'>
    <div className='col-md-12 '>
      <table className='table table-dark tabel-hover table-striped'>
        <thead>
          <tr>
            <th>s.no</th>
            <th>name</th>
            <th>age</th>
          </tr>
        </thead>
        <tbody>
          {
            student.map((x,n)=>{
              return(
                <tr key={n}>
                  <td>{n+1} </td>
                  <td>{x.name}</td>
                  <td>{x.age}</td>
                </tr>
              )
            })
          }
        </tbody>
      </table>
    </div>
  </div>
</div>


<Banner/>

</>

    )
}

export default Contact